const AbstractInt = require('./AbstractInt');
const Endian = require('./../../Endian');
const BC = require('./../../BC');

/**
 * Fields type for an 8 Bit value.
 */
class Int8 extends AbstractInt {

  /**
   * Constructor.
   *
   * @param {String} id
   * @param {Boolean} unsigned
   */
  constructor(id, unsigned) {
    super(id, unsigned, Endian.LITTLE_ENDIAN);
  }

  /**
   * Reads the int8 value.
   *
   * @param {BC} bc
   * @returns {Number}
   */
  decodeFromBytes(bc) {
    return bc.readInt8(0, this.unsigned, this.endian);
  }

  /**
   * Appends the given int8 value.
   *
   * @param {Number} value
   * @returns {BC}
   */
  encodeToBytes(value) {
    return BC.fromInt8(value, this.unsigned, this.endian);
  }

  /**
   * Gets the size in bytes.
   *
   * @returns {number}
   */
  get size() {
    return 1;
  }
}

module.exports = Int8;
